@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;700&family=Libre+Baskerville:wght@400;700&family=Manrope:wght@600;700;800&family=Merriweather:wght@300;400;700&family=Outfit:wght@300;400;700&family=Overpass:wght@400;500;600;700&family=Plus+Jakarta+Sans:wght@500;800&family=Tiro+Telugu&display=swap");
@import "./core/style/variables.scss";

// Reset
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html,
body {
  height: 100%;
}

html {
  /* 1 rem = 10px; 10px/16px = 62.5% */
  font-size: 62.5%;
  font-family: "DM Sans", sans-serif;
}

// General style
h1 {
  font-size: 2.6rem;
  font-weight: 700;
  margin-bottom: 5rem;
}

h2 {
  font-size: 1.4rem;
  font-weight: 400;
  color: $mediumBrown;
}

h3 {
  font-size: 1.3rem;
  font-weight: 400;
  margin-bottom: 0.8rem;
}
